    color: $default-selection-text-color;
    text-shadow: none;
}

::selection {
    background-color: $default-selection-bg-color;
    color: $default-selection-text-color;
    text-shadow: none;
}

::-webkit-selection {
    background-color: $default-selection-bg-color;
    color: $default-selection-text-color;
    text-shadow: none;
}

img::-moz-selection {
    background: transparent;
}

img::selection {
    background: transparent;
}

/* Basic Typography */
h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
    font-family: inherit;
    color: inherit;
    line-height: 1.25;
    margin: 0 0 15px 0;
    padding: 0;
    font-weight: normal;
}

h1, .h1 {
    font-size: 3.6em;
}

h2, .h2 {
    font-size: 3.1em;
}

h3, .h3 {
    font-size: 2.6em;
}

h4, .h4 {
    font-size: 2.35em;
}

h5, .h5 {
    font-size: 1.85em;
}

h6, .h6 {
    font-size: 1em;
}

small, .small {
    font-size: 0.7em;
}

a.link {
    text-decoration: none;
}

a.link:hover {
    text-decoration: underline;
}

p {
    margin: 0 0 15px 0;
}

hr {
    border-bottom: 1px solid #333333;
    border-left: none;
    border-right: none;
    border-top: none;
    margin: 1.5em 0;
    clear: both;
}

ul.list-inline, ol.list-inline {
    list-style: none;
    margin: 0;
    padding: 0;
}

ul.list-inline li, ol.list-inline li {
    display: inline-block;
}

em {
    font-style: italic;
}

strong {
    font-weight: bold;
}

abbr[title] {
    border-bottom: 1px dotted;
}

abbr, acronym {
    cursor: help;
}

tt, code {
    font-family: "Consolas", "Monaco", "Bitstream Vera Sans Mono", "Courier", monospace;
    font-size: 0.75em;
}

pre {
    font-family: "Consolas", "Monaco", "Bitstream Vera Sans Mono", "Courier", monospace;
    -moz-tab-size: 2;
    tab-size: 2;
    margin-bottom: 12px;
    white-space: nowrap;
    border: 1px solid #E1E1E1;
    border-radius: 4px;
}

pre > code {
    display: block;
    padding: 1rem 1.5rem;
    white-space: pre;
}

/* Media elements */
img, picture, video, audio, embed, object, input, iframe {
    max-width: 100%;
    margin: 0;
}

img {
    display: inline-block;
    vertical-align: middle;
    border: 0;
    -ms-interpolation-mode: bicubic;
}

img[src*=".svg"] {
    width: 100% \9
;
}

a:hover img {
    border: none;
    background: none;
}

x::-ms-reveal, img[src*=".svg"] {
    width: 100%;
}

a img {
    border: none;
}

/* Button Module */
button, .button {
    cursor: pointer;
    text-decoration: none;
    background-color: transparent;
    padding: 0;
    border: 0;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    white-space: nowrap;
}

.button {
    display: inline-block;
    text-align: center;
    vertical-align: middle;
}

button {
    background-image: none;
}

button:focus, button:active {
    outline: none;
    box-shadow: none;
    border: none;
}

button.button-disabled, button[disabled] {
    cursor: not-allowed;
    opacity: 0.65;
}

/* Header module */
/* Form Module */
fieldset {
    border: 0;
    margin: 0;
    padding: 0;
}

textarea, select {
    padding: 6px 10px;
    box-shadow: none;
}

textarea, input {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

textarea {
    resize: none;
    height: auto;
    min-height: 50px;
}

.table {
    width: 100%;
    margin-bottom: 15px;
}

.table th, .table td {
    border-bottom: .1rem solid #e1e1e1;
    padding: 10px 20px;
    text-align: left;
}

.table th:first-child, .table td:first-child {
    padding-left: 0;
}

.table th:last-child, .table td:last-child {
    padding-right: 0;
}

/* General layout */
section {
    width: 100%;
    display: block;
    position: relative;
}

section:before, section:after {
    content: " ";
    display: table;
}

section:after {
    clear: both;
}

.inner-container {
    width: 100%;
}

.inner-container:before, .inner-container:after {
    content: " ";
    display: table;
}

.inner-container:after {
    clear: both;
}

.text-center {
    text-align: center;
}

.text-left {
    text-align: left;
}

.text-right {
    text-align: right;
}

.text-uppercase {
    text-transform: uppercase;
}

.full-width {
    width: 100%;
}

.spacing {
    padding: 15px 0;
}

/* PRINT STYLES */
@media print {
    *, *:before, *:after, *:first-letter, *:first-line {
        background: transparent !important;
        color: #000 !important;
        box-shadow: none !important;
        text-shadow: none !important;
    }
    a, a:visited {
        text-decoration: underline;
    }
    a[href]:after {
        content: " (" attr(href) ")";
    }
    abbr[title]:after {
        content: " (" attr(title) ")";
    }
    a[href^="#"]:after,
    a[href^="javascript:"]:after {
        content: "";
    }
    pre, blockquote {
        border: 1px solid #999;
        page-break-inside: avoid;
    }
    img {
        max-width: 100% !important;
        page-break-inside: avoid;
    }
    p, h2, h3 {
        orphans: 3;
        widows: 3;
    }
    h2, h3 {
        page-break-after: avoid;
    }
}
